.search-box {
  align-items: center;
  border-radius: 5px;
  display: flex;
  flex: 1;
  justify-content: center;
  margin: 5px;
  position: relative; }
  .search-box > .search.icon {
    fill: #ccc;
    position: absolute;
    left: .75rem; }
  .search-box:focus-within > .search.icon {
    fill: currentColor; }
  .search-box > .input {
    flex: 1;
    padding: .25rem .25rem .25rem 1.75rem; }
  .search-box > .search-results {
    background: #fff;
    border-radius: 0 0 5px 5px;
    border: 1px solid var(--color-primary);
    box-shadow: 3px 3px 3px #33333333;
    left: 0;
    line-height: 1.5rem;
    list-style: none;
    margin: -3px 8px;
    padding: .1rem;
    position: absolute;
    right: 0;
    top: 100%;
    z-index: 5; }
    .search-box > .search-results > .search-result {
      padding: .15rem; }
      .search-box > .search-results > .search-result[aria-selected="true"] {
        background: var(--color-system); }
      .search-box > .search-results > .search-result > .preview {
        font-size: .8rem; }
