.wiki.layout {
  background: #f6f6f6;
  background: var(--color-background);
  display: flex;
  flex: 1;
  flex-direction: column;
  height: 100vh;
  margin: 0;
  max-height: 100vh;
  max-width: 100vw;
  min-height: 100vh;
  min-width: 100vw;
  padding: 0;
  width: 100vw; }
  .wiki.layout > .content {
    display: flex;
    flex-direction: row;
    flex: 1;
    height: 100%;
    width: 100%; }
    .wiki.layout > .content > .navigation {
      width: 150px; }
    .wiki.layout > .content > .page {
      flex: 1;
      max-height: calc(100vh - 44px);
      width: 100%; }
    .wiki.layout > .content > .navigation ~ .page {
      width: calc(100vw - 150px); }
