* {
  box-sizing: border-box; }

html {
  overflow: hidden; }

body {
  font-family: Arial, Calibri, sans-serif;
  font-family: var(--font-primary);
  margin: 0;
  padding: 0; }

a {
  color: #08f;
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }
  a.missing {
    color: #c44; }

button {
  background: #ffffff55;
  background: -webkit-linear-gradient(top, #ffffff55 0%, #ffffff55 20%, #ffffff00 80%, #ffffff00 100%);
  background: linear-gradient(to bottom, #ffffff55 0%, #ffffff55 20%, #ffffff00 80%, #ffffff00 100%);
  border: 1px outset #66666655;
  border-radius: .25rem;
  box-shadow: 0 0 0 1px #66666655;
  cursor: pointer;
  padding: .25rem .4rem; }
  button:focus {
    box-shadow: 0 0 0 3px rgba(226, 251, 251, 0.5); }
  button.danger {
    background-color: #c44;
    color: white; }
  button.safe {
    background-color: #97cd76;
    color: #fff;
    font-weight: bold;
    text-shadow: 1px 1px 1px gray; }
  button.link {
    border: none;
    box-shadow: none; }

fieldset {
  border-style: solid; }
  fieldset > legend {
    font-weight: bold; }

hr {
  border-color: var(--color-primary);
  border-style: solid;
  border-width: 1px 0 0 0; }

input, textarea {
  border-radius: 5px;
  border: 1px solid rgba(1, 1, 1, 0.25);
  flex: 1;
  outline: none;
  margin: .25rem;
  padding: .25rem; }
  input:focus, textarea:focus {
    border: 1px inset; }

svg {
  fill: currentColor;
  height: 1rem;
  shape-rendering: geometricPrecision;
  width: 1rem; }
  svg.x2 {
    height: 2rem;
    width: 2rem; }

.danger {
  border-color: #d76c6c;
  color: #c44; }

.row {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: flex-start; }
  .row > * {
    margin: .25rem; }

:root {
  --palette-beige:       #fdf5e6;
  --palette-blue-light:  #e2fbfb;
  --palette-blue-royal:  #08f;
  --palette-blue:        #42afe3;
  --palette-gold-dark:   #998100;
  --palette-gold:        #ffd700;
  --palette-gray-dark:   #333;
  --palette-gray-light:  #d3d6db;
  --palette-gray-medium: #999;
  --palette-green:       #97cd76;
  --palette-offwhite:    #f6f6f6;
  --palette-orange:      #e8720c;
  --palette-red:         #c44;
  --palette-white:       #fff;
  --color-bg-button:     #e9e9e9;
  --color-bg-inverse:    #42afe3;
  --color-bg-modal:      rgba(246, 246, 246, 0.75);
  --color-bg-warning:    #ffd700;
  --color-bg:            #f6f6f6;
  --color-book:          #e2fbfb;
  --color-border:        #42afe3;
  --color-combo:         #deefd3;
  --color-danger-bg:     #d76c6c;
  --color-danger-text:   white;
  --color-danger:        #c44;
  --color-highlight:     rgba(226, 251, 251, 0.5);
  --color-info:          #e2fbfb;
  --color-link-missing:  #c44;
  --color-link:          #08f;
  --color-shadow:        rgba(211, 214, 219, 0.75);
  --color-success:       #97cd76;
  --color-system:        #fdf5e6;
  --color-table-bg-even: #fff;
  --color-table-bg-odds: #f6f6f6;
  --color-text-inverse:  #f6f6f6;
  --color-text:          #333;
  --color-warning:       #998100; }
