.flyout.menu {
  background: inherit;
  border: 1px solid transparent;
  color: inherit;
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 0;
  position: relative; }
  .flyout.menu.open {
    z-index: 100; }
    .flyout.menu.open > .menu-toggler {
      border: solid #00000033;
      border-radius: 5px 5px 0 0;
      border-width: 1px 1px 0 1px; }
    .flyout.menu.open > .menu-toggler:before, .flyout.menu.open > .flyout-panel:before {
